



@media (min-width: 1112px) {
.dialogsWrapper {
    height: 100%;
    width: 55vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: minmax(250px,auto) minmax(max-content,3fr);


}}
@media (max-width:  1111px) {
    .dialogsWrapper{
        display: grid;
        grid-template-columns: minmax(150px,auto) minmax(max-content,2fr);
    }

}

